import * as actionTypes from "../actions/actions";
import texts_3x3 from "../texts/text_3x3";

const initialState = {
  allTexts: {},
  textCounter: 0,
  t: null,
  b: null,
  tF: null,
  bF: null,
  c: null,
  p: null,
  r: null,
  zero: true,
  zeroPrev: false,
  zeroFC: true,
  deleteTwo: true,
  products: {},
  productArray: [],
  product: null,
  carry: null,
  starting: true,
  closing: false,
  finallResultCount: false,
  finallResults: [],
  finallResultCarries: [],
  finallResultCarry: 0,
  fC: 0,
};

let nto, text;

text = texts_3x3();

const result = (state = initialState, action) => {
  if (action.type === actionTypes.END) {
    return {
      ...initialState,
      products: {},
      productArray: [],
      finallResults: [],
      finallResultCarries: [],
    };
  }

  if (action.type === actionTypes.NEXT) {
    let tR = action.resultDigits.tR.filter((c) => typeof c === "number");
    let bR = action.resultDigits.bR.filter((c) => typeof c === "number");
    let mult;
    let pA = [...state.productArray];
    state.zeroPrev = false;
    if (!state.finallResultCount) {
      if (state.starting) {
        mult = tR[tR.length - 1] * bR[bR.length - 1];
        state.carry = Math.floor(mult / 10);
        state.product = mult % 10;
        state.productArray = [];
        if (tR.length - 1 === 0) {
          state.productArray.push(
            mult % 10,
            Math.floor(mult / 10) ? Math.floor(mult / 10) : null
          );
        } else state.productArray.push(state.product);
        let initP = 0;
        state.products = {
          [initP]: state.productArray,
        };
        return {
          ...state,
          textCounter: state.textCounter + 1,
          allTexts: {
            ...state.allTexts,
            [state.textCounter]: text[state.textCounter].text,
          },
          starting: false,
          t: tR.length - 1,
          b: bR.length - 1,
          tF: 2,
          bF: 2,
          c: 0,
          p: 0,
          r: 0,
        };
      } else {
        if (state.t === 0) {
          state.zeroFC = false;
        }
        if (state.t === tR.length - 1 && !state.zero) {
          state.zeroFC = true;
        }
        if (state.zero) {
          if (state.t === 0) {
            state.carry = 0;
          } else {
            state.carry = action.resultDigits.carries[state.c][state.p];
          }
          if (state.t === 0) {
            state.t = tR.length - 1;
            state.b = state.b - 1;
            state.c = 0;
            state.tF = 2;
            state.bF = state.bF - 1;
          } else {
            state.t = state.t - 1;
            state.tF = state.tF - 1;
            state.c = state.c + 1;
          }
          if (state.t === 0) {
            state.zero = false;
            state.deleteTwo = true;
            mult = tR[state.t] * bR[state.b] + state.carry;
            pA.push(
              mult % 10,
              Math.floor(mult / 10) ? Math.floor(mult / 10) : null
            );
            state.productArray = pA;
            state.product = null;
          } else {
            state.zeroFC = true;
            mult = tR[state.t] * bR[state.b] + state.carry;
            state.product = mult % 10;
            pA.push(state.product);
            state.productArray = pA;
          }
          state.products = {
            ...state.products,
            [state.p]: pA,
          };
        } else {
          if (state.t === 0 && state.b === 0) {
            state.finallResultCount = true;
            state.zeroFC = false;
          } else {
            state.zero = true;
            state.zeroPrev = true;
            state.carry = 0;
            if (state.p !== bR.length - 1) {
              state.p = state.p + 1;
            }
            state.productArray = [...Array(state.p).fill(0)];
            state.products = {
              ...state.products,
              [state.p]: state.productArray,
            };
          }
        }
      }
    } else {
      if (bR.length === 1) {
        return {
          ...state,
          closing: true,
        };
      }
      if (state.fC === bR.length * 2 + 1) {
        return {
          ...state,
          closing: true,
        };
      }
      let size = Object.keys(state.products).length;
      let add = 0;
      for (let i = 0; i < size; i++) {
        if (state.products[i][state.fC] !== undefined) {
          add = add + state.products[i][state.fC];
        }
      }
      add = add + state.finallResultCarry;
      let fR = [...state.finallResults];
      if (state.fC === bR.length * 2 + 1) {
        fR.push(add % 10 ? add % 10 : null);
      } else {
        fR.push(add % 10);
      }
      state.finallResultCarry = Math.floor(add / 10);
      state.finallResultCarries.push(state.finallResultCarry);
      state.fC = state.fC + 1;
      state.finallResults = fR;
    }
    return {
      ...state,
      textCounter: state.textCounter + 1,
      allTexts: {
        ...state.allTexts,
        [state.textCounter]: text[state.textCounter].text,
      },
    };
  }

  if (action.type === actionTypes.PREV) {
    const newTextsObjectCreate = (texts) => {
      let valuesTextsArr, keysTextsArr, removedTextsArr;
      let newTextsObj = {};

      valuesTextsArr = Object.values(texts);
      valuesTextsArr.pop();
      removedTextsArr = [...valuesTextsArr];
      keysTextsArr = Object.keys(texts);
      for (let i = 0; i < removedTextsArr.length; i++) {
        newTextsObj[keysTextsArr[i]] = removedTextsArr[i];
      }

      return newTextsObj;
    };
    nto = newTextsObjectCreate(state.allTexts);
    let finalReturnObj, productsPosition, productsArray;
    let tR = action.resultDigits.tR.filter((c) => typeof c === "number");
    let bR = action.resultDigits.bR.filter((c) => typeof c === "number");
    state.carry = 0;
    if (!state.finallResultCount) {
      if (state.t === null && state.b === null) {
        finalReturnObj = {
          closing: true,
        };
      } else {
        if (state.t === tR.length - 1 && state.b === bR.length - 1) {
          finalReturnObj = {
            ...initialState,
            products: {},
            productArray: [],
            finallResults: [],
            finallResultCarries: [],
          };
        } else {
          if (state.t === tR.length - 1 && !state.zeroPrev) {
            state.zeroFC = false;
          }
          if (state.t === 0 && state.zeroPrev) {
            state.zeroFC = true;
          }
          if (!state.zeroPrev) {
            state.productArray = [...state.products[state.p]];
            if (state.t === tR.length - 1) {
              state.zeroPrev = true;
              state.productArray.splice(state.productArray.length - 1, 1);
            } else {
              state.zeroFC = true;
              let spliceN;
              if (state.deleteTwo) {
                spliceN = 2;
                state.deleteTwo = false;
              } else {
                spliceN = 1;
              }
              state.productArray.splice(
                state.productArray.length - spliceN,
                spliceN
              );
            }
            productsArray = [...state.productArray];
            productsPosition = state.p;
            if (state.t === tR.length - 1) {
              state.t = 0;
              state.tF = 3 - tR.length;
              if (state.b !== bR.length - 1) {
                state.b = state.b + 1;
                state.bF = state.bF + 1;
              }
              state.c = tR.length - 1;
            } else {
              state.t = state.t + 1;
              state.tF = state.tF + 1;
              state.c = state.c - 1;
            }
            state.zero = true;
          } else {
            state.zero = false;
            state.zeroPrev = false;
            state.deleteTwo = true;
            state.productArray = [];
            productsPosition = state.p;
            productsArray = [];
            if (state.p !== 0) {
              state.p = state.p - 1;
            }
          }
          finalReturnObj = {
            products: {
              ...state.products,
              [productsPosition]: productsArray,
            },
          };
        }
      }
    } else {
      if (state.fC === 0) {
        state.finallResultCount = false;
        state.zeroFC = true;
      } else {
        state.fC = state.fC - 1;
        let fR = [...state.finallResults];
        let fRC = [...state.finallResultCarries];
        fR.splice(fR.length - 1, 1);
        fRC.splice(fRC.length - 1, 1);
        console.log(state.fC - 1);
        if (state.fC - 1 === 0) {
          state.finallResultCarry = 0;
        } else if (state.fC - 1 !== -1) {
          state.finallResultCarry = fRC[state.fC - 1];
        } else {
          state.finallResultCarry = 0;
        }
        finalReturnObj = {
          finallResults: fR,
          finallResultCarries: fRC,
        };
      }
    }
    if (state.textCounter !== 0) {
      state.textCounter = state.textCounter - 1;
    }
    return {
      ...state,
      allTexts: {
        ...nto,
      },
      ...finalReturnObj,
    };
  } else {
    return state;
  }
};

export default result;
