import * as actionTypes from "../actions/actions";

const initialState = {
  boardTitle: {
    title: "",
    show: false,
  },
  btnHide: true,
  nxtBtn: true,
  input: false,
  message: "",
  tr: null,
  br: null,
  c: null,
  p: null,
  r: null,
  carry: null,
  result: {},

  valueEnter: false,

  correctCarryCell: false,
  correctResultRow: 0,
  correctResultCell: false,

  carryClick: true,
  resultClick: true,
  carryFocused: false,
  resultFocused: false,
  carryAdded: false,
  resultAdded: false,
  carryFocusedFirst: false,

  doubleResult: false,
  dr1Added: false,
  addZero: false,
  zeroToBeAdded: [],
  continueAddingZeroCheck: 0,

  finalResult: false,
  finalResultNext: false,
  finalResultInit: true,
};

const removeHightlightsFromCells = () => {
  document
    .getElementById("carryInputContainer")
    .childNodes.forEach((c) => (c.style.backgroundColor = "transparent"));
  document
    .querySelectorAll("#resultInput")
    .forEach((c) => (c.style.backgroundColor = "transparent"));
};

const practiceMode = (state = initialState, action) => {
  switch (action.type) {
    case actionTypes.PRACTICESTART:
      const tR = action.topRow.join("");
      const bR = action.bottomRow.join("");
      const n1 = action.topRow[tR.length - 1];
      const n2 = action.bottomRow[bR.length - 1];
      const ans = n1 * n2;
      let ra = [];
      for (let i = 0; i <= bR.length; i++) {
        ra.push([]);
      }
      return {
        ...state,
        boardTitle: {
          title: `We will muiltiply <span>${tR}</span> and <span>${bR}</span> step by step together. Our first step will be entering the correct values in the  highlighted area. And then <span>click the next button to continue.</span>`,
          show: true,
        },
        btnHide: false,
        tr: tR.length - 1,
        br: bR.length - 1,
        c: 0,
        p: 0,
        r: String(Number(tR) * n2).length - 1,
        input: true,
        message: `${n1} X ${n2} = ${ans}, here we have carry ${Math.floor(
          ans / 10
        )} and result ${Math.floor(
          ans % 10
        )}. Click on which cell they will go ?`,
        correctCarryCell: 4,
        correctResultCell: 5,
        result: { ...ra },
      };
    case actionTypes.PRACTICENEXT:
      let returnObjNxt = {};
      removeHightlightsFromCells();
      let t, b, c, p, r, cc, crr, crc;
      t = state.tr;
      b = state.br;
      c = state.c;
      p = state.p;
      r = state.r;
      crr = state.correctResultRow;
      crc = state.correctResultCell;

      if (
        state.result[action.r.bR.length - 1][0] ===
        action.r.resultsRows[action.r.bR.length - 1][0]
      ) {
        state.finalResult = true;
        state.carryClick = false;
      }
      if (!state.finalResult) {
        if (!state.addZero) {
          if (t === 0 && b >= 0) {
            t = action.r.tR.length - 1;
            b--;
            crr++;
            r = action.r.resultsRows[crr].length - 1;
            crc = 5;
            const zeroToBeAdded = [
              ...Array(action.r.bR.length - 1 - b).fill(true),
            ];
            zeroToBeAdded.push(false);
            returnObjNxt = {
              addZero: true,
              zeroToBeAdded: zeroToBeAdded,
              message: "Add zeros",
            };
          } else {
            t--;
            r--;
            crc--;
            let n11 = action.r.tR[t];
            let n22 = action.r.bR[b];
            let prevCarry = action.r.carries[state.c][state.p];
            let mult = n11 * n22;
            returnObjNxt = {
              message: `${n11} X ${n22} = ${mult} , Previously we had carry ${prevCarry} after adding that we have ${mult} + ${prevCarry} = ${
                mult + prevCarry
              } . Now, we have carry ${action.r.carries[c][p]} and result of ${
                action.r.resultsRows[crr][r]
              }`,
              carryClick: true,
            };
          }
          if (c === action.r.tR.length - 1) {
            c = 0;
            p++;
          } else {
            c++;
          }
          if (t === 0) {
            if (action.r.resultsRows[crr].length !== action.r.bR.length) {
              state.doubleResult = true;
            }
          }
          switch (state.c + 1) {
            case 0:
              cc = 4;
              break;
            case 1:
              cc = 3;
              break;
            case 2:
              cc = 2;
              break;
            default:
              cc = 4;
              break;
          }
        } else {
          let n11 = action.r.tR[t];
          let n22 = action.r.bR[b];
          let mult = n11 * n22;
          r--;
          cc = 4;
          crc--;
          returnObjNxt = {
            message: `${n11} X ${n22} = ${mult}. Now, we have carry ${action.r.carries[c][p]} and result of ${action.r.resultsRows[crr][r]}`,
            addZero: false,
            carryClick: true,
          };
        }
      } else {
        if (action.r.finalResult.indexOf(action.r.finalResult[0]) === r) {
          returnObjNxt = {
            message: `You Have Done It!`,
          };
        } else {
          returnObjNxt = {
            message: `Add the final result of ${action.r.finalResult[crc + 1]}`,
            nxtBtn: true,
            finalResultNext: false,
          };
        }

        if (state.finalResultInit) {
          state.finalResultInit = false;
          crr++;
          crc = 5;
          r = action.r.finalResult.length - 1;
          t = null;
          b = null;
        } else {
          crc--;
          r--;
        }
      }

      return {
        ...state,
        nxtBtn: true,
        tr: t,
        br: b,
        c: c,
        p: p,
        r: r,
        carry: null,
        correctCarryCell: cc,
        correctResultRow: crr,
        correctResultCell: crc,
        valueEnter: false,
        resultClick: true,
        carryFocused: false,
        resultFocused: false,
        carryAdded: false,
        resultAdded: false,
        ...returnObjNxt,
      };
    case actionTypes.PRACTICEINPUT:
      let returnObj, nxtReturn;
      if (!state.finalResult) {
        if (!state.addZero) {
          if (state.carryFocusedFirst && state.resultFocused) {
            state.carryFocused = false;
          }
          if (state.carryFocused || state.resultFocused) {
            if (state.carryFocused) {
              const correctCarry = action.r.carries[state.c][state.p];
              if (action.key === correctCarry) {
                state.carryAdded = true;
                returnObj = {
                  carry: action.key,
                  message: "Correct!",
                  carryFocused: false,
                };
              } else {
                returnObj = {
                  carry: action.key,
                  message: "Wrong! Try again.",
                };
              }
            } else if (state.resultFocused) {
              if (!state.doubleResult) {
                let keyObj = [...state.result[state.correctResultRow]];
                keyObj[state.correctResultCell] = action.key;
                const correctResult =
                  action.r.resultsRows[state.correctResultRow][state.r];
                if (action.key === correctResult) {
                  let rfrc = {};
                  if (state.carryFocusedFirst && !state.carryAdded) {
                    rfrc = {
                      carryFocused: true,
                      carryFocusedFirst: false,
                      message: "Correct! Now enter the carry number.",
                    };
                  } else {
                    rfrc = {
                      message: "Correct!",
                    };
                  }
                  state.resultAdded = true;
                  returnObj = {
                    result: {
                      ...state.result,
                      [state.correctResultRow]: keyObj,
                    },
                    resultFocused: false,
                    ...rfrc,
                  };
                } else {
                  returnObj = {
                    result: {
                      ...state.result,
                      [state.correctResultRow]: keyObj,
                    },
                    message: "Wrong! Try again.",
                  };
                }
              } else {
                if (!state.dr1Added) {
                  let keyObj = [...state.result[state.correctResultRow]];
                  keyObj[state.correctResultCell] = action.key;
                  const correctResult =
                    action.r.resultsRows[state.correctResultRow][state.r];
                  if (action.key === correctResult) {
                    let rfrc = {};
                    if (state.carryFocusedFirst && !state.carryAdded) {
                      rfrc = {
                        carryFocused: true,
                        carryFocusedFirst: false,
                        message: "Correct! Now enter the carry number.",
                      };
                    } else {
                      rfrc = {
                        message: "Correct!",
                      };
                    }
                    returnObj = {
                      result: {
                        ...state.result,
                        [state.correctResultRow]: keyObj,
                      },
                      correctResultCell: --state.correctResultCell,
                      r: --state.r,
                      resultClick: true,
                      resultFocused: false,
                      dr1Added: true,
                      ...rfrc,
                    };
                  } else {
                    returnObj = {
                      result: {
                        ...state.result,
                        [state.correctResultRow]: keyObj,
                      },
                      message: "Wrong! Try again.",
                    };
                  }
                } else {
                  let keyObj = [...state.result[state.correctResultRow]];
                  keyObj[state.correctResultCell] = action.key;
                  const correctResult =
                    action.r.resultsRows[state.correctResultRow][state.r];
                  if (action.key === correctResult) {
                    let rfrc = {};
                    if (state.carryFocusedFirst && !state.carryAdded) {
                      rfrc = {
                        carryFocused: true,
                        carryFocusedFirst: false,
                        message: "Correct! Now enter the carry number.",
                      };
                    } else {
                      rfrc = {
                        message: "Correct!",
                      };
                    }
                    state.resultAdded = true;
                    returnObj = {
                      result: {
                        ...state.result,
                        [state.correctResultRow]: keyObj,
                      },
                      carryAdded: true,
                      resultFocused: false,
                      doubleResult: false,
                      dr1Added: false,
                      ...rfrc,
                    };
                  } else {
                    returnObj = {
                      result: {
                        ...state.result,
                        [state.correctResultRow]: keyObj,
                      },
                      message: "Wrong! Try again.",
                    };
                  }
                }
              }
            }
            if (state.carryAdded && state.resultAdded) {
              setTimeout(() => {
                action.asyncDispatch({
                  type: actionTypes.PRACTICENEXT,
                  r: action.r,
                });
              }, 1000);
            }
          } else if (state.carryClick && !state.resultClick) {
            action.asyncDispatch({ type: actionTypes.KEYPADHIDE });
            returnObj = {
              message: "Please select the carry cell.",
            };
          } else if (!state.carryClick && state.resultClick) {
            action.asyncDispatch({ type: actionTypes.KEYPADHIDE });
            returnObj = {
              message: "Please select the result cell.",
            };
          } else if (state.carryAdded && state.resultAdded) {
            action.asyncDispatch({ type: actionTypes.KEYPADHIDE });
            returnObj = {
              message:
                "You have done it please click the next button to continue.",
            };
          } else {
            action.asyncDispatch({ type: actionTypes.KEYPADHIDE });
            returnObj = {
              message: "Please select the cells first!",
            };
          }
        } else {
          if (state.zeroToBeAdded[state.continueAddingZeroCheck]) {
            if (state.resultFocused) {
              let keyObj = [...state.result[state.correctResultRow]];
              keyObj[state.correctResultCell] = action.key;
              if (action.key === 0) {
                let cazc = state.continueAddingZeroCheck;
                returnObj = {
                  result: {
                    ...state.result,
                    [state.correctResultRow]: keyObj,
                  },
                  resultFocused: false,
                  message: "Correct!",
                };
                if (!state.zeroToBeAdded[state.continueAddingZeroCheck + 1]) {
                  setTimeout(() => {
                    action.asyncDispatch({
                      type: actionTypes.PRACTICENEXT,
                      r: action.r,
                    });
                  }, 1000);
                } else {
                  nxtReturn = {
                    resultClick: true,
                    correctResultCell: state.correctResultCell - 1,
                    r: state.r - 1,
                    continueAddingZeroCheck: cazc + 1,
                  };
                }
              } else {
                returnObj = {
                  result: {
                    ...state.result,
                    [state.correctResultRow]: keyObj,
                  },
                  message: "Wrong! Try again.",
                };
              }
            } else {
              action.asyncDispatch({ type: actionTypes.KEYPADHIDE });
              returnObj = {
                message: "Please select the cells first!",
              };
            }
          } else {
            action.asyncDispatch({ type: actionTypes.KEYPADHIDE });
            returnObj = {
              message:
                "You have done it please click the next button to continue.",
            };
          }
        }
      } else {
        if (state.finalResultNext) {
          action.asyncDispatch({ type: actionTypes.KEYPADHIDE });
          returnObj = {
            message:
              "You have done it please click the next button to continue.",
          };
        } else {
          if (state.resultFocused) {
            let keyObj = [...state.result[state.correctResultRow]];
            keyObj[state.correctResultCell] = action.key;
            const correctResult = action.r.finalResult[state.r];
            if (action.key === correctResult) {
              returnObj = {
                result: {
                  ...state.result,
                  [state.correctResultRow]: keyObj,
                },
                resultFocused: false,
                message: "Correct!",
                resultClick: true,
                finalResultNext: true,
              };
              setTimeout(() => {
                action.asyncDispatch({
                  type: actionTypes.PRACTICENEXT,
                  r: action.r,
                });
              }, 1000);
            } else {
              returnObj = {
                result: {
                  ...state.result,
                  [state.correctResultRow]: keyObj,
                },
                message: "Wrong! Try again.",
              };
            }
          } else {
            action.asyncDispatch({ type: actionTypes.KEYPADHIDE });
            returnObj = {
              message: "Please select the cells first!",
            };
          }
        }
      }
      return {
        ...state,
        ...returnObj,
        ...nxtReturn,
      };
    case actionTypes.PRACTICEMESSAGE:
      return {
        ...state,
        message: action.m,
      };
    case actionTypes.PCF:
      if (!state.resultFocused) {
        state.carryFocusedFirst = true;
      }
      return {
        ...state,
        carryFocused: action.v,
        carryClick: !action.v,
      };
    case actionTypes.PRF:
      return {
        ...state,
        resultFocused: action.v,
        resultClick: !action.v,
      };
    case actionTypes.PRACTICEEND:
      return {
        boardTitle: {
          title: "",
          show: false,
        },
        btnHide: true,
        nxtBtn: true,
        input: false,
        message: "",
        tr: null,
        br: null,
        c: null,
        p: null,
        r: null,
        carry: null,
        result: {},

        valueEnter: false,

        correctCarryCell: false,
        correctResultRow: 0,
        correctResultCell: false,

        carryClick: true,
        resultClick: true,
        carryFocused: false,
        resultFocused: false,
        carryAdded: false,
        resultAdded: false,
        carryFocusedFirst: false,

        doubleResult: false,
        dr1Added: false,
        addZero: false,
        zeroToBeAdded: [],
        continueAddingZeroCheck: 0,
      };
    default:
      return state;
  }
};

export default practiceMode;
