import * as actionTypes from "../actions/actions";

const initialState = {
  answer: { carries: [] },
};

const multiplication = (state = initialState, action) => {
  if (
    action.type === actionTypes.START ||
    action.type === actionTypes.PRACTICESTART
  ) {
    let tR = action.topRow;
    let bR = action.bottomRow;
    let allProducts = [];
    let allResults = [];
    let product = "";
    let carries = [];

    for (let i = bR.length - 1; i >= 0; i--) {
      let d2 = bR[i];
      let carry = 0;
      for (let j = tR.length - 1; j >= 0; j--) {
        let d1 = tR[j];
        let mult = d2 * d1 + carry;
        carry = Math.floor(mult / 10);
        carries.push(carry);
        product = (mult % 10) + product;
      }
      product = (carry % 10) + product;
      let shift = 10 ** (bR.length - i - 1);
      allResults.push(product * shift);
      allProducts.push(product);
      product = "";
    }
    const finalResult = String(allResults.reduce((a, c) => a + c))
      .split("")
      .map((num) => {
        return Number(num);
      });
    let rows = [];
    for (var i = 0; i < bR.length; i++) {
      rows[i] = String(allProducts[i])
        .split("")
        .map((num) => {
          return Number(num);
        });
    }
    let resultsRows = [];
    for (var r = 0; r < bR.length; r++) {
      resultsRows[r] = String(allResults[r])
        .split("")
        .map((num) => {
          return Number(num);
        });
    }
    let c = [];
    for (let j = 0; j < tR.length; j++) {
      let k = j;
      let t = [];
      for (let i = 0; i < tR.length; i++) {
        t.push(carries[k]);
        k = k + tR.length;
      }
      c.push(t);
    }

    return {
      ...state,
      answer: {
        ...state.answer,
        tR: tR,
        bR: bR,
        carries: c,
        result: rows,
        resultsRows: resultsRows,
        finalResult: finalResult,
      },
    };
  } else {
    return state;
  }
};

export default multiplication;
