import React from "react";
import ReactDOM from "react-dom";
import "./index.css";
import App from "./App";
import { BrowserRouter as Router } from "react-router-dom";
import { applyMiddleware, compose, combineReducers, createStore } from "redux";
import { Provider } from "react-redux";
import reportWebVitals from "./reportWebVitals";

import numbers from "./store/reducers/numbers";
import results from "./store/reducers/results";
import multiplication from "./store/reducers/multiplication";
import numberPad from "./store/reducers/numberPad";
import sidemenu from "./store/reducers/sidemenu";
import practiceMode from "./store/reducers/practiceMode";

const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;

const asyncDispatchMiddleware = (store) => (next) => (action) => {
  let syncActivityFinished = false;
  let actionQueue = [];

  function flushQueue() {
    actionQueue.forEach((a) => store.dispatch(a)); // flush queue
    actionQueue = [];
  }

  function asyncDispatch(asyncAction) {
    actionQueue = actionQueue.concat([asyncAction]);

    if (syncActivityFinished) {
      flushQueue();
    }
  }

  const actionWithAsyncDispatch = Object.assign({}, action, { asyncDispatch });

  const res = next(actionWithAsyncDispatch);

  syncActivityFinished = true;
  flushQueue();

  return res;
};

const rootReducer = combineReducers({
  number: numbers,
  result: results,
  multi: multiplication,
  numpad: numberPad,
  menu: sidemenu,
  pMode: practiceMode,
});

const store = createStore(
  rootReducer,
  composeEnhancers(applyMiddleware(asyncDispatchMiddleware))
);

ReactDOM.render(
  <Provider store={store}>
    <React.StrictMode>
      <Router basename="/math-app-live">
        <App />
      </Router>
    </React.StrictMode>
  </Provider>,
  document.getElementById("root")
);

reportWebVitals();
export default store;
